<template>
  <div>
	<el-row class="HeadTop">
  		<el-col :span="row"><div class="grid-content bg-purple-dark"><span class="title">{{headTop.name}}</span></div></el-col>
  		<el-col :span="2" v-for="(item,index) in menu[0]" :key="index"><div class="grid-content bg-purple-dark">{{item.menu}}</div></el-col>
	</el-row>
  </div>
</template>

<script>
	import { mapState, mapActions } from 'vuex'
	export default {
		name: 'HeadTop',
		data () {
		    return {
		      	row:0
		    }
		},
		computed:{
			...mapState(['menu','headTop'])
		},
		methods:{
			...mapActions(['setMenu']),
		},
		created(){
			const _this = this;
			this.setMenu().then((data)=>{
				console.log(data);
				this.$emit('setMenu',data,_this.menu[0]);
				_this.row = 24-(_this.menu[0].length)*2
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.HeadTop {
	width: 100%;
	height: 3rem;
	color: #fff;
	line-height: 3rem;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
}
.title {
	font-size: 1.5rem;
	padding: 0 0.5rem;
}
.el-col {
	text-align: center;
}
.el-col:nth-of-type(1) {
	text-align: left;
}
.grid-content {
	height: 3rem;
	line-height: 3rem;
}
.bg-purple-dark {
	background: rgba(255,255,255,0);
}
</style>



