import Vue from 'vue'
import axios from 'axios'
import { getMenu } from '../api/api'


export const setMenu = (context,data) => {
	return new Promise((resolve,reject)=>{
		getMenu().then(data=>{
			context.commit('setMenu',data);
			resolve('success')
		}).catch(error=>{
			reject('error');
		})
	})
}


