<template>
	<div class="topImage">
		<div class="content">
			<span>{{topImage.monicker}}</span>
			<p style="font-size:1rem;">{{topImage.sendWord}}</p>
		</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	export default {
		name:'topImage',
		data(){
			return {
				
			}
		},
		computed:{
			...mapState(['img','topImage'])
		},
		methods:{

		},
		created(){

		}
	}
</script>

<style>
	.topImage  {
		display: block;
		width: 100%;
		min-height: 40%;
		max-height: 48%;
		color:#fff;
		position: relative;
		background-image: url('/static/images/424211.jpg');
		background-size: cover;
		background-clip: padding-box;
		background-repeat:no-repeat;
		background-position: center;
	}
	.content {
		position: absolute;
	    left: 0;
	    top: 50%;
	    width: 100%;
	    text-align: center;
	    font-size: 4rem;	
	}
</style>


